.buttons {
  @include list-reset;

  display: flex;
  justify-content: space-between;

  margin-bottom: 90px;
  margin-left: 32px;
  width: 900px;

  border-bottom: 1px solid rgba(44, 46, 63, 0.3);

  @media (max-width: 1023px) {
    margin-left: 38px;
    margin-bottom: 33px;
  }

  @media (max-width: 767px) {
    margin-left: 15px;
    margin-bottom: 26px;
  }
}

.buttons__item {
  padding: 15px 0 10px;

  &:last-child {
    margin-right: 0;
  }
}

.buttons__link {
  font-size: 20px;
  line-height: 24px;
  font-weight: 500;
  text-decoration: none;

  letter-spacing: -0.415px;
  color: rgba(44, 46, 63, 0.3);
  background-color: transparent;
  border: 0;

  &:active {
    color: $peach;
  }

  &:hover {
    color: $blue;
    cursor: pointer;
  }

  &:active,
  &:focus {
    outline: none;
  }
}
